.userContainer {
  width: 100%;
  height: 90vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  .tableInfo {
    width: 100%;
    height: 10vh;

    display: flex;
    justify-content: space-between !important;
    align-items: center;
    padding-left: 20px;
    padding-right: 20px;

    .spanstyle {
      font-weight: bold;
      font-size: 20px !important;
    }
    .ant-input-group-addon {
      button {
        height: 100% !important;
        min-height: 32px;
      }
    }

    .ant-select-selection-placeholder {
      font-size: 14px !important;
    }
    .ant-select-selection-item {
      font-size: 14px !important;
    }
    .ant-select-arrow {
      font-size: 14px !important;
    }

    .ant-btn {
      span {
        font-size: 14px !important;
      }
    }
  }

  .tableData {
    min-height: 70vh;
    padding-left: 20px;
    padding-right: 20px;
  }

  .paginationComp {
    width: 100%;
    min-height: 10vh;
    display: flex;
    justify-content: center;
    align-items: center;

    .ant-pagination-item-active {
      background-color: black !important;
    }

    svg {
      color: white !important;
    }
    a {
      color: white !important;
    }
  }

  .models {
    .modal {
      display: none; /* Hidden by default */
      position: fixed; /* Stay in place */
      z-index: 1; /* Sit on top */
      padding-top: 100px; /* Location of the box */
      left: 0;
      top: 0;
      width: 100%; /* Full width */
      height: 100%; /* Full height */
      overflow: auto; /* Enable scroll if needed */
      background-color: rgb(0, 0, 0); /* Fallback color */
      background-color: rgba(0, 0, 0, 0.4);
    }

    /* Modal Content */
    .modal-content {
      background-color: #fefefe;
      margin: auto;
      padding: 20px;
      border: 1px solid #888;
      width: 80%;
      height: 100%;
    }

    /* The Close Button */
    .close {
      color: #aaaaaa;
      float: right;
      font-size: 28px;
      font-weight: bold;
    }

    .close:hover,
    .close:focus {
      color: #000;
      text-decoration: none;
      cursor: pointer;
    }
  }
}
.formContainer {
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding: 10px;
  padding-top: 20;

  .inputContainer {
    display: flex;
    justify-content: space-between;

    .spanData {
      span {
        font-weight: bold;
      }
    }

    .inputdata {
      width: 70%;
    }
  }
}
