.userContainer {
  width: 100%;
  height: 90vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  .tableInfo {
    width: 100%;
    height: 10vh;

    display: flex;
    justify-content: space-between !important;
    align-items: center;
    padding-left: 20px;
    padding-right: 20px;

    .spanstyle {
      font-weight: bold;
      font-size: 20px !important;
    }
    .ant-input-group-addon {
      button {
        height: 100% !important;
        min-height: 32px;
      }
    }

    .ant-select-selection-placeholder {
      font-size: 14px !important;
    }
    .ant-select-selection-item {
      font-size: 14px !important;
    }
    .ant-select-arrow {
      font-size: 14px !important;
    }

    .ant-btn {
      span {
        font-size: 14px !important;
      }
    }
  }

  .tableData {
    min-height: 70vh;
    padding-left: 20px;
    padding-right: 20px;
  }

  .paginationComp {
    width: 100%;
    min-height: 10vh;
    display: flex;
    justify-content: center;
    align-items: center;

    .ant-pagination-item-active {
      background-color: black !important;
    }

    svg {
      color: white !important;
    }
    a {
      color: white !important;
    }
  }
}
