.userContainer {
  width: 100%;
  height: 70vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  color: black;

  .tableInfo {
    width: 100%;
    height: 10vh;
    display: flex;
    justify-content: center;
    align-items: center;

    span {
      font-weight: bold;
      font-size: 18px;
    }
  }

  .tableData {
    min-height: 50vh;
    padding-left: 20px;
    padding-right: 20px;
  }

  .paginationComp {
    width: 100%;
    min-height: 10vh;
    display: flex;
    justify-content: center;
    align-items: center;

    .ant-pagination-item-active {
      background-color: black !important;
    }

    svg {
      color: white !important;
    }
    a {
      color: white !important;
    }
  }
}

.formContainer {
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding: 10px;
  padding-top: 20;

  .inputContainer {
    display: flex;
    justify-content: space-between;

    .spanData {
      span {
        font-weight: bold;
      }
    }

    .inputdata {
      width: 70%;
    }
  }
}
