.login-form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  grid-gap: 10px;
  height: 100vh;
  width: 100vw;
  background-color: rgba(39, 253, 150, 0.8);
  color: black;
  padding: 20px;

  .inputFlex {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

/* .ant-btn span {
  background: transparent !important;
} */

@media (max-width: 768px) {
  .login-image {
    display: none;
  }
  .login-form {
    width: 100%;
  }
}
